body {
  align-content: flex-start;
  align-items: center;
  font-family: Arial, Helvetica, sans-serif;
  color: white;
  line-height: 1.4;
  background: radial-gradient(rgba(87,231,249,1) 25%, rgba(22,205,77,1)); height:100vh;
  background-repeat: no-repeat;
  background-attachment: scroll;
}

.App {
  text-align: center;
}


.App-header {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
