.Hamburger{
  display:grid;
  grid-template-rows: repeat(3, 1fr);
  grid-row-gap: .25em;
  width: 2em;
  grid-area: hamburger;
  align-self: center;
  cursor: pointer;
  transition: .2s
}
.Hamburger:hover {
  transform: scale(1.05);
}
.Hamburger > div {
  background-color: white;
  height: .25em;

}
.Navigation {
  height: 3em;
  width: 100vw;
  top: 0;
  margin-bottom: 3em;
  display: grid;
  grid-template-columns: 2em 2em auto 4em;
  grid-template-areas: "margin logo space hamburger";
  background-color: #5353ac;
  position:fixed;
  z-index: 3;
  box-shadow: 0 7px 9px 0 rgba(0, 0, 0, 0.2);
}
.logo{
  align-self: center;
  grid-area: logo;
  cursor: pointer;
}

.OpenNavigation{
  background: rgb(83, 83, 172);
  height: 100vh;
  width: 100vw;
  position:fixed;
  display: grid;
  grid-template-columns: auto 6em;
  grid-auto-rows: 3em 1fr 1fr 1fr 1fr 1fr 1fr;
  grid-template-areas:
    "margin exit"
    "MenuItem1 MenuItem1"
    "MenuItem2 MenuItem2"
    "MenuItem3 MenuItem3"
    "MenuItem4 MenuItem4"
    "MenuItem5 MenuItem5";
  z-index: 3;
  transition: 5s ease-in-out;
}
.OpenNavigation > div {
  cursor: pointer;
  color: white;
  font-weight: bold;
  width: 100%;
  align-items:center;
  transition: .3s ease-in
}
.OpenNavigation > div:hover {
  transform: scale(1.12);
}
.Exit {
  grid-area: exit;
  font-size: 2em;
  align-self: center;
  cursor: pointer;
  justify-self: start;
}
.MenuItem1{
  font-size: 3em;
  grid-area:MenuItem1;
  align-self: center;
  justify-self: center;
}
.MenuItem2{
  font-size: 3em;
  grid-area:MenuItem2;
  align-self: center;
  justify-self: center;
}
.MenuItem3{
  font-size: 3em;
  grid-area:MenuItem3;
  align-self: center;
  justify-self: center;
}
.MenuItem4{
  font-size: 3em;
  grid-area:MenuItem4;
  align-self: center;
  justify-self: center;
}
.MenuItem5{
  font-size: 3em;
  grid-area:MenuItem5;
  align-self: center;
  justify-self: center;
}
