.V-card {
  margin: auto;
  background-color: white;
  min-width: 15em;
  width: 45vw;
  overflow: hidden;
  border-radius: .5em;
  cursor: pointer;
  box-shadow: 0 6px 8px 0 rgba(0, 0, 0, 0.2), 0 8px 20px 0 rgba(0, 0, 0, 0.19);
  margin-top: 3em;
  transition-duration: .25s;
  display:grid;
  grid-template-rows: repeat(2, auto);
  grid-template-areas: "header" "body";

}
.V-card:hover{
  transform:scale(1.025);
}

.V-card-header {
  height: 15em;
  background: black;
  overflow: hidden;
  margin: 0;
  grid-area: header;
  display: grid;
  grid-template-rows: repeat(4, 1fr);
  grid-template-columns: 1.5em auto 5em;
}

.Header-photo {
  grid-column: 1/4;
  grid-row: 1/5;
  border-radius: .5em, .5em, 0, 0;
  opacity: .8;
  z-index: 0;
}
h4.location {
  color: white;
  grid-column: 2/3;
  grid-row:1/2;
  justify-self: start;
  align-self: center;
  z-index: 1;

}
h2.V-title {
  color: white;
  background: rgba(89, 89, 89,.5);
  grid-column: 1/4;
  grid-row:4/5;
  align-self: stretch;
  padding: 1em;
  z-index: 1;
}

.V-card-body {
  grid-area: body;
  display: grid;
  grid-template-rows: 2em  auto 3.5em;
  grid-template-columns: 1.5em auto;
}
p.V-cost {
  color: #808080;
  grid-column: 2 / 4;
  justify-self: start;
  z-index: 1;
  background: none;
}
p.description {
  grid-row: 2 / 3;
  grid-column: 1 / 4;
  align-self: center;
  background: none;
  color: black;
  box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.19);

}
button.V-button {
  grid-row: 3 / 4;
  grid-column: 2 / 4;
  justify-self: start;
  align-self: center;
  background: none;
  color: black;
  padding: 0;
  margin: 0;

}
button.V-button:hover{
  transform: translateX(1em);
}
