.Form {
  padding-top: 2vh;
  text-align: center;
  align-content: flex-start;
  align-items: center;
  font-family: Arial, Helvetica, sans-serif;

}
div.Welcome{
  padding-top: 10vh;
}

h1.Welcome{
  padding-bottom: 10vh;
  font-size: 3em;
}
h1.Form{
  padding: 3vh 0 3vh 0;
}
h1.Form1{
  padding-top:3vh;
}


input {
  display: block;
  min-width: 70%;
  margin: 0 auto;
  padding: 1em;
  width: 35em;
  border-radius: 8px;
  border-style: none;
  border: 1px;
  transition: 0.7s ease;

}
p {
  font-size: 1em;
  color: white;
  background-color: rgba(25, 34, 54, .5);
  padding: 1.5vh 0 1.5vh 0;
}

button {
  margin: 0.8em;
  color: white;
  font-size: 1.1em;
  border-radius: 25px;
  padding: 1em;
  border: none;
  background: #eb7873;
  transition: .75s ease;
}
button:hover{
  background:palevioletred;
  color: white;
  cursor: pointer;
  transform: scale(1.1);
}


* {
	-webkit-box-sizing: border-box;
	-moz-box-sizing: border-box;
	box-sizing: border-box;
	margin: 0;
	padding: 0;
}
body {
	font-size: 16px;
  background: linear-gradient(185deg, rgba(87,231,249,1) 4%, rgba(22,205,77,1) 100%); height:100vh;
  background-repeat: no-repeat;
  height:100%auto;
  background-attachment: fixed;
}
a {
	color: $primary;
}


input[type="radio"]{
  display:none;
  position: relative;
  vertical-align: middle;
}

label{
  padding: 5px 15px;
  font-size: 1em;
  border-radius: 2em;
  vertical-align: middle;
  font-size: 1.5em;
  transition: 0.5s ease-in;
}
label:hover{
  padding: 5px 15px;
  transition: 1s ease;
  transform-origin: center;
  background-color: #a6c5f7;
  cursor: pointer;
}

div.Radio{
  padding: 1.75vh;
}

input[type="radio"]:checked + label{
  padding: 5px 15px;
  background:#0774D9;
  border-radius:2em;
  color:#fff;
}

input[type="radio"]:checked + label:before {
  transition: 0.5s ease-in;
}
---------------------------

.info {
	text-align: center;
	padding: 20px;
	color: $titular;
	border-bottom: 1px solid #ccc;
}
locationSelector {
	color: black;
  width: 50vw;
  vertical-align: middle;
}

span.remove:hover{
  background-color: #a6c5f7;
  cursor: pointer;
}
